<script>
import config from "@/common/config";
// import utils from "@/common/utils.js";
import service from "@/common/service";
import moment from "moment";
export default {
  // el: "#app",
  data() {
    return {
      user: null,
      forms: [],
      form: {},
      moment: moment,
      config: config,
      searchKey: ''
    };
  },
  computed:{
    //模糊搜索
    filterData: function() {
      var input = this.searchKey && this.searchKey.toLowerCase();
      var items = this.forms;
      var items1;
      if (input) {
        items1 = items.filter(function(item) {
          return Object.keys(item).some(function(key1) {
            return String(item[key1])
                    .toLowerCase()
                    .match(input);
          });
        });
      } else {
        items1 = items;
      }
      return this.sortByKey(items1,'name','asc');
    }
  },
  created: function () {
    // $("#formsMenu").hide();
    // $("#uploadMenu").show();
    this.refresh();
  },
  methods: {
    sortByKey(array,key,order){
      if (order === 'asc'){
        return array.sort(function(a,b){
          var x = a[key];
          var y = b[key];
          return ((x<y)?-1:(x>y)?1:0)
        })
      }else if (order === 'desc'){
        return array.sort(function(b,a){
          var x = a[key];
          var y = b[key];
          return ((x<y)?-1:(x>y)?1:0)
        })
      }
    },
    refresh: function () {
      //  var forms = [];
      this.form = {};
      service.getItems("form").then((forms) => {
        this.forms = forms;
        console.log(forms);
      });
    },
    selectForm(form) {
      this.form = form;
      Formio.use(FormioContrib);
      Formio.createForm(
        document.getElementById("formData"),
        {
          components: form.formData,
        },
        {
          language: "ch",
          i18n: config.i18n,
        }
      ).then(function (form) {});
    },
    editForm() {
      if (!this.form || !this.form._id) {
        return;
      }
      this.$router.push({
        name: "formEdit",
        query: { id: this.form._id },
      });
    },
    deleteForm() {
      if (!this.form._id) {
        return;
      }
      service.deleteItem("form", this.form._id).then((result) => {
        this.refresh();
        $("#deleteModal").modal("hide");
      });
    },
  },
};
</script>

